import moment from 'moment';
import Head from 'next/head';
import Link from 'next/link';
import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/router';
import LanguageIcon from '@mui/icons-material/Language';
import { Container, LinearProgress, Table, TableHead, TableRow, TableCell } from '@mui/material';
import { sendApiRequest, getUrlApp } from '../../../lib/api/public';
import Custom404 from '../../../components/common/Custom404';

export async function getServerSideProps(context) {
    const { organizationId } = context.query;

    let response;
    let organizationResponse;

    try {
        response = await sendApiRequest({
            path: `/portal/jobs/?organizationId=${organizationId}`,
            method: 'GET',
        });


        organizationResponse = await sendApiRequest({
            path: `/portal/organization-detail?_id=${organizationId}`,
            method: 'GET',
        });
    } catch (e) {
        return {
            props: {
                initialJobs: [],
                initialOrganization: null,
            },
        };
    }

    if (!response.error) {
        return {
            props: {
                initialJobs: response,
                initialOrganization: organizationResponse,
            },
        };
    }

    return {
        props: {
            initialJobs: [],
            initialOrganization: null,
        },
    };
}

const Jobs = ({ initialJobs, initialOrganization }: { initialJobs: any, initialOrganization: any }) => {
    const [jobs, setJobs] = useState(initialJobs);
    const [organization, setOrganization] = useState(initialOrganization);
    const [isLoading, setLoading] = useState(false);
    const [notFound, setNotFound] = useState(false);

    const router = useRouter();
    const organizationId = router.query.organizationId;

    const getJobs = () => {
        setLoading(true);

        sendApiRequest({
            path: `/portal/jobs/?organizationId=${organizationId}`,
            method: 'GET',
        })
            .then((response: any) => {
                setLoading(false);

                if (!response.error) {
                    setJobs(response);
                }
            })
            .catch((e) => {
                setLoading(false);
                setJobs([]);
            });
    };

    const getOrganization = () => {
        sendApiRequest({
            path: `/portal/organization-detail?_id=${organizationId}`,
            method: 'GET',
        })
            .then((response: any) => {
                setOrganization(response);
            });
    };

    useEffect(() => {
        getJobs();
        getOrganization();
    }, [organizationId]);

    if (notFound) {
        return <Custom404 />;
    }

    let metaDescription = `Jobs at ${organization.name}`;

    const title = `Jobs at ${organization.name}`;

    if (!organization) {
        return null;
    }

    return (
        <div id="portal-jobs">
            <Head>
                <title>{title} </title>
                < meta name="description" content={metaDescription} />
            </Head>

            {isLoading ? <LinearProgress /> : null}

            <div className="navbar">
                <Container>
                    <div style={{ display: 'flex' }}>
                        <LanguageIcon />

                        <Link
                            href={organization.website}
                            style={{ textDecoration: 'none', marginLeft: '5px' }}
                            target="_blank"
                            className="blue-link"
                        >
                            Visit our website
                        </Link>
                    </div>
                </Container>
            </div>

            <div className="header">
                <h1>Jobs at {organization.name}</h1>
            </div>

            <Container>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Job Title</TableCell>
                            <TableCell>Location</TableCell>
                            <TableCell>Posted At</TableCell>
                        </TableRow>

                        {jobs.map((job) => (
                            <TableRow key={job._id}>
                                <TableCell>
                                    <Link href={`${getUrlApp()}/${organization._id}/carreers/jobs/${job._id}`} target="_blank" className="blue-link">
                                        {job.title}
                                    </Link>
                                </TableCell>
                                <TableCell>{job.location}</TableCell>
                                <TableCell>{moment(job.createdAt).format('DD/MM/YYYY')}</TableCell>
                            </TableRow>
                        ))}
                    </TableHead>
                </Table>
            </Container>
        </div>
    );
};

export default Jobs;
